body {
  margin: 0;
  padding: 0;
  font-family:'Open Sans', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.tickername {
    cursor: pointer;
    color: blue;
}

.tickername:hover {
    text-decoration: underline;
}
.metainfo {
    padding: 20px;
    margin: 20px 0px;
    background-color: #f5f4f4;
    border-radius: 5px;
}
.stats {
    font-weight: bold;
}
.count {
    font-weight: normal;
    color: #2dd62d;
}
.pagination {
    padding: 10px 0px;
}
.action-btn {
    margin: 2px;
}
.Modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.Modal-dialog{
    width: 700px;
    min-height: 400px;
    background: #fff;
    padding: 50px;
    margin: 0 auto;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    box-shadow: 1px 1px 1px 1px #585656;
    border-radius: 5px;    
}
.Modal-main {
  position:fixed;
  background: white;
  width: 80%;
  height: auto;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
.ticker-list {
    padding: 0px;
}
.ticker-list li{
    list-style: none;
    padding: 8px 10px;
    margin: 5px 0px;
    font-size: 15px;
    border: 1px solid #e8e8e8;
    font-weight: bold;
}
.detail-span {
    font-weight: normal;
    color: #007bff;
}
